export default {
	name: 'UserInfo',
	props: {
		account:{
			type:String,
			default: '',
		},
		img:{
			type:String,
			default: '',
		},
		realName:{
			type:String,
			default: '',
		},
		age:{
			type:String,
			default: '',
		},
		fontColor:{
			type:String,
			default: '#ffffff',
		}
	},
	methods: {
		login() {
			uni.redirectTo({
				url: '/pages/login/login'
			});
		}
	}
};
